* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  flex-wrap: wrap;
  gap: 20px;
  list-style: none;
  display: flex;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: 1400px;
  margin: auto;
}

.search {
  background: #00f;
  padding: 15px 0;
}

form {
  justify-content: center;
  gap: 15px;
  display: flex;
}

form button {
  cursor: pointer;
  background: #fafafa;
  border: none;
  border-radius: 5px;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 600;
  transition: all .25s;
}

form button:hover {
  color: #fafafa;
  background: green;
}

form input {
  width: 500px;
  border: none;
  border-radius: 5px;
}

.item {
  width: calc(25% - 20px);
  border: 1px solid #000;
}

.img {
  width: 100%;
  padding-bottom: 65%;
  position: relative;
}

.img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.info {
  display: flex;
}

.info-item {
  width: 25%;
  border-right: 1px solid #000;
  padding: 5px;
}

.info-item:last-child {
  border-right: unset;
}

.head {
  text-align: center;
  font-size: 12px;
  font-weight: 900;
  line-height: 1.2;
  display: block;
}

.text {
  text-align: center;
  font-size: 14px;
